*{
  color:white
}

/* RecentSearchBar.css */
.container {
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding-top: 10px;
}

.input-container {
  display: flex;
  width: 40%;
}

input[type="text"] {
  padding: 8px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 30px;
  color: black;
}

button {
  padding: 8px 16px;
  font-size: 16px;
  border: none;
  border-radius: 30px;
  background-color: #007bff;
  color: white;
  cursor: pointer;
}
.search-button-img{
  height: 50%;
  width: 50%;
}
button:hover {
  background-color: #0056b3;
}

.recent-searches-container {
  margin-left:17.5%;
  position:absolute;
  border-radius: 4px;
  width: 13%;
  height: fit-content;
  overflow-y: auto;
  background-color: white;
}

.recent-searches ul {
  list-style-type: none;
  padding: 0;
}

.recent-searches li {
  padding: 5px 10px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  color: black;
}

.recent-searches li:hover {
  background-color: #f0f0f0;
}
#temp{
  height:200px;
  width: 200px;
}
.weather-info{
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 5%;
}
.humidity-wind{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 50%;
  margin-top: 5%;
}
#humidity-wind-icons{
  height: 50px;
  width: 50px;
}
.humidity-container,.windspeed-container{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}
.humid-right,.windspeed-right{
  margin-left: 10px;
}
.temp-head{
  display: flex;
  flex: wrap;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
h1{
  font-size: 3rem;
}
h2{
  font-size: 4rem;
}
p{
  font-size: 1.4rem;
}
.title{
  font-size: 2rem;
  font-weight: 700;
}
.search-input-button{
  margin-left: 10px;
  border-radius: 30px;
}

.main-app-contianer{
  width: 50vw;
  margin: auto;
}
@media only screen and (min-width: 0px) and (max-width: 500px) {
  .main-app-contianer{
    height: fit-content;
    width: 100vw;
  }
  .container{
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .input-container{
    justify-content: center;
  }
}
@media only screen and (min-width: 501px) and (max-width: 900px) {
  .main-app-contianer{
    height: 100vh;
    width: 100vw;
  }
}
@media only screen and (min-width: 901px) and (max-width: 1499px) {
  .main-app-contianer{
    height: 100vh;
    width: 90vw;
  }
}
@media only screen and (min-width: 1500px) {
  .main-app-contianer{
    height: 100vh;
    width: 50vw;
  }
}